class Container extends React.Component {
  state = { isMounted: true };

  render() {
    const { isMounted = true } = this.state;
    return (
      <>
      <div className="App">

<Card width={this.width-600} height={this.height-600} dark elevation={5}>
      <header className="App-header">

        <h1>
          Jayde Marie Bell - Artist Portfolio
          <Divider dark />
        </h1>

        <p>
        <Button dark large rounded>Home</Button>
        <Button dark large rounded>About</Button>
        <Button dark large rounded>Contact</Button>
        <Button onClick={() =>
            this.setState(state => ({ isMounted: !state.isMounted }))
          } dark large rounded>{isMounted ? "StopVisuals" : "StartVisuals"}</Button>
        <Divider dark />
        </p>

      </header>
      </Card>
    </div>
        {/* <button
          onClick={() =>
            this.setState(state => ({ isMounted: !state.isMounted }))
          }
        >
          {isMounted ? "StopVisuals" : "StartVisuals"}
        </button> */}
        {isMounted && <App />}
        {/* {isMounted && <div>Scroll to zoom, drag to rotate</div>} */}
      </>
    );
  }
}

